import React, { useEffect, useState } from "react";
import axios from "axios";

function Test() {
    const [cart, setCart] = useState([]);
    const [memberId, setMemberId] = useState(1);
    const [shopId, setShopId] = useState(3);
    const [carts, setCarts] = useState([]);
    // const [review, setReview] = useState({
    //     reviewId: '',
    //     content: '',
    //     memberName: '',
    //     shopName: ''
    // });

    const addCart = () => {
        axios.post("http://localhost:8181/cart/register", {
            memberId: memberId,
            shopId: shopId
        })
            .then(response => {
                console.log(' .');
            })
            .catch(error => {
                console.error('    :', error);
            });
    };

    useEffect(() => {
        axios.get("http://localhost:8181/cart/findall", {
        })
            .then(res => {
                setCarts(res.data);
                console.log(' ');
            })
            .catch(e => {
                console.log('', e);
            });
    }, []); //    , useEffect      

    // const handleChange = (e) => {
    //     const { name, value } = e.target;
    //     setReview(prev => ({
    //         ...prev,
    //         [name]: value
    //     }));
    // };

    const editCart = () => {
        axios.post("http://localhost:8181/cart/edit", cart)
            .then(response => {
                console.log(' .');
            })
            .catch(error => {
                console.error('    :', error);
            });
    };

    const removeCart = () => {
        axios.delete("http://localhost:8181/cart/remove", {
            params:{
                cartId: 1
            }
        })
            .then(res => {
                console.log(" ")
            }).catch(e => {
                console.error(e)
        })
    }

    return (
        <>
            <button type='button' onClick={addCart}></button>

            {carts.map(carts => (
                <div key={carts.id}>
                    <p>Id : {carts.cartId}</p>
                    <p> : {carts.shopName}</p>
                    <p> : {carts.shopPrice}</p>
                    <p>Id : {carts.memberId}</p>
                </div>
            ))}

            <button type='button' onClick={removeCart}></button>
        </>
    );
}

export default Test;
